@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Montserrat", sans-serif;
}
.container {
  text-align: center;
}
p,
blockquote {
  font-size: 0.9em;
  line-height: 1.6em;
}
ul {
  list-style-type: none;
}
